<template>
	<section class="mine">
		<div v-show='mineType == 1' class="mine-head" @click="login">
			<!-- <div class="head-left">
				<span>登录/注册</span>
				<span>亲，您还没有登录哦</span>
			</div>
			<div class="head-right">
				<img src="../images/user01.png"/>
			</div> -->
			<div>
				<img src="../images/user01.png" alt="">
			</div>
			<div>
				<span>登录/注册</span>
			</div>
		</div>
		<div v-show='mineType == 2' class="mine-head">
			<!-- <div class="head-left">
				<span>{{phone}}</span>
				<span>编辑个人资料</span>
			</div>
			<div class="head-right">
				<img src="../images/user02.png"/>
			</div> -->
			<div>
				<img src="../images/user02.png" alt="">
			</div>
			<div>
				<span>{{phone}}</span>
			</div>
			<div class="edit" @click="already">
				编辑资料
			</div>
		</div>
		<div class="mineContent">
			<div @click='already'>
				<mt-cell is-link title="个人中心" class="borrow">
					<img slot="icon" src="../images/center.png">
				</mt-cell>
			</div>
			<mt-cell is-link to='/problem' title="常见问题" class="borrow">
				<img slot="icon" src="../images/question.png">
			</mt-cell>
			<div @click='callPhone'>
				<mt-cell is-link title="贷款咨询" class="borrow">
					<img slot="icon" src="../images/zixun.png">
				</mt-cell>
			</div>
		</div>
	</section>
  
</template>
<script>
	import { MessageBox, Toast } from 'mint-ui'
	import { getUser } from './../api/api'
	import { getStore } from './../config/mUtils'
	import { mapMutations } from 'vuex'
export default {
  data () {
    return {
    	mineType: 1,
    	phone: null
    	
    }
  },
  methods: {
  	...mapMutations([
			'TYPE_DHH'
		]),
    login() {
    	this.$router.push({path: '/login'})
    },
    already() { // 个人中心登录与否
    	if (this.mineType === 1) {
    		this.$router.push({path: '/login'})
    	} else {
    		this.$router.push({path: '/personal'})
    	}
    },
    callPhone() { // 贷款咨询弹出框
    	MessageBox.confirm('', {
			title: '',
			message: '客服热线',
			confirmButtonText: '呼叫'
		}).then(action => {
			window.location.href="tel:0571-88003589"
		})
    },
    init() {
    	this.TYPE_DHH(4)
    	var userId = getStore('userId')
    	let params = {
    		userId: userId
    	}
		getUser(params).then(res => {
    		if (res.code === 200) {
    			if (res.data.realName !== null) {
    				this.phone = res.data.realName
    			} else {
    				this.phone = res.data.phone.substr(0, 3) + '****' + res.data.phone.substr(res.data.phone.length - 4);
    			}
		    	if (this.phone !== null) {
		    		this.mineType = 2
		    	} else {
		    		this.mineType = 1
		    	}
    		} else {
    			Toast({
					message: res.msg,
					position: 'bottom',
					duration: 2000
				});
    		}
    	})
    }
  },
  mounted() {
  	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
.mine {
	flex:1;
	background: #f3f5f8;
	
	.mineMy {
		width: 100%;
		height: 0.8rem;
		background: white;
		
		span {
			display: inline-block;
			width: 100%;
			height: 0.8rem;
			line-height: 0.8rem;
			font-size: 0.3rem;
			text-align: center;
			font-weight: 600;
		}
	}
	.mine-head {
		width: 100%;
		height: 2.58rem;
		background-image: linear-gradient(to top,#EC6B4D,#D94F4D);
		background-image: -webkit-linear-gradient(to top,#EC6B4D,#D94F4D);
		text-align: center;
		position: relative;
		div {
			img {
				width: 1.35rem;
				margin-top: 0.5rem;
				margin-bottom: 0.2rem;
			}
			span {
				color: #fff;
				font-size: 0.26rem;
				letter-spacing: 0.03rem;
			}
		}
		.edit {
			height: 0.61rem;
			line-height: 0.61rem;
			padding: 0 0.3rem;
			position: absolute;
			top: 0.8rem;
			right: 0;
			background: #854446;
			border-top-left-radius: 0.5rem;
			border-bottom-left-radius: 0.5rem; 
			color: #fff;
			font-size: 0.22rem;
			letter-spacing: 0.022rem;
		}
		// .head-left {
		// 	display: inline-block;
		// 	width: 48%;
		// 	height: 2rem;
		// 	margin-left: 0.2rem;
			
		// 	span {
		// 		display: inline-block;
		// 		width: 100%;
		// 	}
		// 	span:nth-child(1) {
		// 		font-size: 0.25rem;
		// 		font-weight: 600;
		// 		margin-top: 0.6rem;
		// 	}
		// 	span:nth-child(2) {
		// 		font-size: 0.15rem;
		// 		color: #999999;
		// 		margin-top: 0.4rem;
		// 	}
		// }
		// .head-right {
		// 	float: left;
		// 	width: 20%;
		// 	height: 2rem;
		// 	line-height: 2rem;
		// 	margin-left: 0.2rem;
			
		// 	img:nth-child(1) {
		// 		width: 1.2rem;
		// 		vertical-align: middle;
		// 	}
		// }
	}
	.mineContent {
		width: 100%;
		margin-top: 0.2rem;
		
		.mint-cell-wrapper{
			
		}
		.mint-cell:last-child{
			background-image: none;
		}
		.invite {
			margin-top: 0.2rem;
		}
		.mint-cell {
			height: 0.6rem;
			min-height: 0.74rem;
			margin-bottom: 0.01rem;
			margin-top: 0.2rem;
		.mint-cell-wrapper {
			background-image: none;
			font-size: 0.26rem;
		}
		.mint-cell-allow-right::after {
			width: 0.12rem;
			height: 0.12rem;
			border:0.03rem solid #c8c8cd;
			border-bottom-width:0;
			border-left-width:0;
			right:0.2rem;
		}
			
		}
		// span {
		// 	float: left;
		// }
		img {
			width: 0.25rem;
		}
	}
	
	
	
	.mineCore {
		width: 100%;
		height: 0.6rem;
		line-height: 0.6rem;
		background: white;
		margin-top: 0.2rem;
		
		img:nth-child(1) {
			width: 0.4rem;
			vertical-align: middle;
			margin-left: 0.2rem;
		}
		span:nth-child(2) {
			font-size: 0.22rem;
			display: inline-block;
			width: 81.5%;
			color: black;
			vertical-align: middle;
			margin-left: 0.1rem;
		}
		img:nth-child(3) {
			width: 0.15rem;
			vertical-align: middle;
		}
	}
	
}
.mint-msgbox {
	
	.mint-msgbox-content {
		height: 1rem;
		min-height: 1rem;
		.mint-msgbox-message {
			height: 1rem;
			line-height: 1rem;
			font-size: 0.25rem;
		}
	}
	.mint-msgbox-btns {
		height: 0.6rem;
		line-height: 0.6rem;
		
		.mint-msgbox-btn {
			font-size: 0.25rem;
			color: #b74545;
		}
	}
}
</style>



